html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

html,
body {
    height: 100vh;
}

.calendar-screen {
    display: flex;
    flex-flow: column;

    height: 100vh;
}

.rbc-calendar {
    height: calc(100% - 109px);

    padding: 0 1rem;
}

.bg-dark {
    background-color: #367cf7 !important;
}

/* DatePicker */
.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border: 0 solid gray !important;
}

/* FABs */
.fab,
.fab-danger {
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 50%;

    padding: 1.5625rem;

    height: 3.215rem;
    width: 3.215rem;
}

.fab {
    position: fixed;
    bottom: 1.5625rem;
    right: 3.125rem;
}

.fab-danger {
    position: fixed;
    bottom: 1.5625rem;
    left: 3.125rem;
}
