/* Modal */
.ReactModalPortal > div {
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity 0.2s ease-in-out;
    z-index: 999;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    display: inline;

    background: #fff;
    color: rgb(51, 51, 51);

    max-height: 38.75rem;
    max-width: 31.25rem;

    overflow-y: auto;

    outline: none;

    padding: 0.625rem;
}

.modal--background {
    background-color: rgba(0, 0, 0, 0.3);

    position: fixed;

    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
}
